<template>
  <h1>CAJA</h1>
    <h3>Ingresos= ${{ ingresos }}</h3>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
    computed:{
        ...mapGetters('caja',['ingresos'])
    },
    methods:{
        ...mapActions('caja',['getIngresos'])
    },
    created(){
        this.getIngresos()
    }
}
</script>

